    font-size: 16px;

    color: #FFFFFF;

    padding: 4px 12px;

    height: 27px;

    background: #0FA958;
    border-radius: 4px;
}

.card__line {
    height: 1px;
    width: 100%;

    background: #E0E0E0;
    border-radius: 4px;
}

.card__addition-information {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 14px;
}

.card__period {
    font-family: "Gilroy-Medium", serif;
    font-size: 12px;

    color: #909090;
}

.card__addition-btn {
    font-family: "Gilroy-SemiBold", serif;
    font-size: 16px;

    color: #FFFFFF;

    padding: 0 12px;
    height: 27px;

    text-decoration: none;
    background: #007AFF;
