    font-size: 16px;
    text-decoration: none;

    color: #FFFFFF;
    background: #007AFF;
    border-radius: 4px;
    padding: 8px 0;
}
.preloader__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
}

.preloader {
    position: relative;
    width: 100px;
    height: 70px;
    display: inline-block;
    transform-origin: center;
    -webkit-animation: preloarder 1.5s linear infinite;
            animation: preloarder 1.5s linear infinite;
}

.preloader .spanloader {
    display: flex;
    justify-content: center;
}

.preloader span {
    position: absolute;
    width: 15px;
    height: 15px;
    background: #000;
    transform-origin: center 35px;
    border-radius: 50%;
}

.preloader span:first-child {
    opacity: .1;
}

.preloader span:nth-child(2) {
    transform: rotate(36deg);
    opacity: .2;
}

.preloader span:nth-child(3) {
    transform: rotate(72deg);
    opacity: .3;
}

.preloader span:nth-child(4) {
    transform: rotate(108deg);
    opacity: .4;
}

.preloader span:nth-child(5) {
    transform: rotate(144deg);
    opacity: .5;
}

.preloader span:nth-child(6) {
    transform: rotate(180deg);
    opacity: .6;
}

.preloader span:nth-child(7) {
    transform: rotate(216deg);
    opacity: .7;
}

.preloader span:nth-child(8) {
    transform: rotate(252deg);
    opacity: .8;
}

.preloader span:nth-child(9) {
    transform: rotate(288deg);
    opacity: .9;
}

.preloader span:nth-child(10) {
    transform: rotate(324deg);
    opacity: 1;
}

@-webkit-keyframes preloarder {
    0% {
        transform: rotate(0deg) scale(1);
    }
    100% {
        transform: rotate(360deg) scale(1);
    }
}

@keyframes preloarder {
    0% {
        transform: rotate(0deg) scale(1);
    }
    100% {
        transform: rotate(360deg) scale(1);
    }
}
